* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  max-width: 100%;
  max-height: 100%; }

body, html {
  max-width: 100vw;
  max-height: 100vh;
  width: 100%;
  height: 100%;
  font-size: 90%;
  font-family: "Poppins", Arial, sans-serif;
  line-height: 1.4;
  scroll-behavior: smooth; }

a {
  text-decoration: none;
  color: initial; }

a:hover {
  text-decoration: underline;
  color: rgba(0, 0, 0, 0.7); }

h1, ul, li, span {
  margin: 0; }

header {
  width: 100%;
  padding: 1rem 0;
  transition: .3s; }
  header.active {
    position: fixed;
    box-shadow: 1px 0px 8px rgba(0, 0, 0, 0.6);
    background: white;
    z-index: 55; }

button, input {
  outline: none;
  background: none;
  border: none;
  text-transform: uppercase;
  font-size: .8rem;
  cursor: pointer; }

input {
  color: #3AD34E;
  cursor: initial; }

nav {
  width: 90%;
  padding: .5rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem; }
  nav .menu ul {
    list-style: none; }
  nav .menu li {
    display: inline-block;
    margin-left: 1rem;
    font-size: 1rem; }
  nav .menu .nav-button-close {
    display: none; }
  nav .nav-button {
    display: none;
    width: 2rem; }

.wrapper {
  width: 100%;
  margin: auto; }
  .wrapper .content-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; }
  .wrapper .wrapper-background {
    width: 22rem;
    height: 100%; }
    .wrapper .wrapper-background img {
      height: 60%;
      width: 100%;
      object-position: center;
      object-fit: cover; }
  .wrapper .title, .wrapper .menu {
    padding: .5rem; }
  .wrapper .header-title {
    font-weight: 800;
    text-transform: uppercase; }
  .wrapper .content-wrapper {
    width: 90%;
    height: 80%;
    margin: 0 auto;
    padding: 2rem 0; }
  .wrapper .content {
    padding: 1rem;
    width: 50%; }
  .wrapper .content-title {
    margin: 1rem auto; }
    .wrapper .content-title h1 {
      font-weight: bolder;
      letter-spacing: -2px;
      font-size: 3rem;
      line-height: 3rem; }
  .wrapper .button {
    padding: 1rem 0; }
    .wrapper .button button {
      border: 1px solid #70e000;
      padding: 1rem 3.5rem;
      border-radius: 1.5rem;
      font-weight: bolder;
      color: #9ef01a;
      cursor: pointer;
      transition: background 500ms;
      font-size: 1rem; }
    .wrapper .button button:hover {
      background-color: #38b000; }
  .wrapper .menu-active {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: white;
    z-index: 55; }
    .wrapper .menu-active ul {
      list-style: none; }
      .wrapper .menu-active ul li {
        display: block;
        text-align: center;
        font-size: 2rem;
        padding: 1rem 0; }
        .wrapper .menu-active ul li a {
          color: initial;
          text-decoration: none; }
          .wrapper .menu-active ul li a:hover {
            text-decoration: underline; }
    .wrapper .menu-active .nav-button-close {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      padding: 1rem; }
      .wrapper .menu-active .nav-button-close span {
        font-size: 3rem; }
  .wrapper .arrow-down {
    display: none; }
  .wrapper .our-services-wrapper {
    width: 100%;
    background: #d9ecd0;
    padding: 2rem 0 4rem 0;
    margin-top: 2rem; }
    .wrapper .our-services-wrapper .our-services-title {
