          padding: .5rem 0;
          margin: 1rem 0; }
          .wrapper .about-us-wrapper .about-us-grid .about-us-grid-item .about-us-title h1 {
            font-size: 1.5rem;
            font-weight: bolder;
            letter-spacing: -1px; }
          .wrapper .about-us-wrapper .about-us-grid .about-us-grid-item .about-us-title hr {
