@media screen and (max-width: 959px) {
  body, html {
    font-size: 85%; }
  .content-wrapper {
    height: auto;
    margin-top: 3rem; }
    .content-wrapper .content {
      width: 97%;
      margin: auto; }
      .content-wrapper .content .content-description {
        max-width: 90%; }
  nav {
    width: 100%; }
    nav .nav-button {
      display: initial; }
    nav .menu {
      display: none; }
  .wrapper-background {
    margin: auto; }
  .arrow-down {
    display: flex;
    width: 100%;
    padding: 1.5rem 0;
    justify-content: center;
    align-items: center; } }
